body {
  margin: 0;
  padding: 0;
  color: #fff;
  font-family: Lato, 'sans-serif';
  font-size: 16px;
  width: 100%
}

.title {
  font-weight: 100;
  font-size: 3.5rem;
  margin: 0;
  padding: 0
}

.subtitle {
  font-weight: 100;
  font-size: 1.5rem;
  margin: 0;
  padding: 0
}

.countdown-wrapper {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  height: 100vh;
  -ms-flex-direction: column;
      flex-direction: column
}

.countdown-clock {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  text-align: center;
  margin-top: 32px;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap
}

.countdown-clock div {
  margin-bottom: 16px
}

.countdown-clock p {
  font-size: 1.5rem;
  font-weight: 100;
  margin: 0
}

.countdown-clock span {
  font-size: 2.4rem
}

/***** Slideshow ******/
.slideshow,
.slideshow:after { 
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0px;
    left: 0px;
    z-index: 0; 
    margin: 0;
    padding: 0
}
.slideshow:after { 
    content: '';
    background-image: -webkit-gradient(linear, left top, right top, from(#b8cbb8), color-stop(0%, #b8cbb8), color-stop(0%, #b465da), color-stop(33%, #cf6cc9), color-stop(66%, #ee609c), to(#ee609c));
    background-image: -webkit-linear-gradient(left, #b8cbb8 0%, #b8cbb8 0%, #b465da 0%, #cf6cc9 33%, #ee609c 66%, #ee609c 100%);
    background-image: -o-linear-gradient(left, #b8cbb8 0%, #b8cbb8 0%, #b465da 0%, #cf6cc9 33%, #ee609c 66%, #ee609c 100%);
    background-image: linear-gradient(to right, #b8cbb8 0%, #b8cbb8 0%, #b465da 0%, #cf6cc9 33%, #ee609c 66%, #ee609c 100%);
    opacity: 0.75
}

.slideshow li span { 
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
  color: transparent;
  background-size: cover;
  background-position: 50% 50%;
  background-repeat: none;
  opacity: 0;
  z-index: 0;
  -webkit-animation: imageAnimation 36s linear infinite 0s;
          animation: imageAnimation 36s linear infinite 0s; 
}

.slideshow li:nth-child(1) span { 
  background-image: url('./images/avril.jpg') 
}

.slideshow li:nth-child(2) span { 
  background-image: url('./images/simple-plan.jpg');
  -webkit-animation-delay: 6s;
          animation-delay: 6s; 
}

.slideshow li:nth-child(3) span { 
  background-image: url('./images/bmc.jpg');
  -webkit-animation-delay: 12s;
          animation-delay: 12s; 
}

.slideshow li:nth-child(4) span { 
  background-image: url('./images/cruj.jpg');
  -webkit-animation-delay: 18s;
          animation-delay: 18s; 
}

.slideshow li:nth-child(5) span { 
  background-image: url('./images/blink182.jpg');
  -webkit-animation-delay: 24s;
          animation-delay: 24s; 
}

.slideshow li:nth-child(6) span { 
  background-image: url('./images/harry.jpg');
  -webkit-animation-delay: 30s;
          animation-delay: 30s; 
}

/***** ANIMATION *****/
@-webkit-keyframes imageAnimation { 
    0% { opacity: 0; -webkit-animation-timing-function: ease-in; animation-timing-function: ease-in; }
    8% { opacity: 1; -webkit-animation-timing-function: ease-out; animation-timing-function: ease-out; }
    17% { opacity: 1 }
    25% { opacity: 0 }
    100% { opacity: 0 }
}
@keyframes imageAnimation { 
    0% { opacity: 0; -webkit-animation-timing-function: ease-in; animation-timing-function: ease-in; }
    8% { opacity: 1; -webkit-animation-timing-function: ease-out; animation-timing-function: ease-out; }
    17% { opacity: 1 }
    25% { opacity: 0 }
    100% { opacity: 0 }
}

@media screen and (min-width : 600px) {
  .countdown-clock {
    -ms-flex-direction: row;
        flex-direction: row;
  }

  .countdown-clock div {
    margin: 10px
  }
}



